<template>
  <div id="consentcookie-configurator" class="consentcookie-configurator">
    <div class="nav">
      <router-link to="/apps" class="router-link" disabled="true">Applicaties</router-link>
      <span @click.capture="routeClick">
        <router-link to="/design" :class="['router-link',{disabled:noAppsSelected}]" :title="noAppsSelectedTitle">Design</router-link>
      </span>
      <span @click.capture="routeClick">
        <router-link to="/contents" :class="['router-link',{disabled:noAppsSelected}]" :title="noAppsSelectedTitle">Teksten</router-link>
      </span>
      <button v-show="!noAppsSelected && copyButtonEnabled" class="btn cta-copy" @click="copyScript()" v-clipboard:copy="script">Kopieer script</button>
    </div>
    <router-view/>
  </div>
</template>

<script>

  import { isEmpty } from 'lodash';
  import * as constants from '@/internal/constants';

  export default {
    name: 'app',
    data() {
      return {};
    },
    computed: {
      noAppsSelected() {
        return isEmpty(this.$services.config.getConfigVal(this.$store.state.config, constants.DEFAULT_CONFIG_KEY_APPS_CONSENT));
      },
      noAppsSelectedTitle() {
        return this.noAppsSelected ? 'Maak eerst een selectie van applicaties.' : '';
      },
      copyButtonEnabled() {
        return this.$store.state.options && this.$store.state.options.copyButton !== false;
      },
      script() {
        return this.$store.getters.script;
      },
    },
    methods: {
      copyScript() {
        this.$router.push('script');
      },
      routeClick($e) {
        if (this.noAppsSelected) {
          $e.preventDefault();
        }
      },
    },
    created() {
      this.$router.push('apps');
    },
    mounted() {
      this.$events.$emit('mounted', {});
    },
  };
</script>

<style lang="scss" scoped>

  @import '../assets/scss/mixins';
  @import '../assets/scss/general';

  $nav-height: 50px;

  #consentcookie-configurator {

    .nav {
      background: $color-white;
      border-bottom: 1px solid rgba(230, 231, 225, 1);
      height: $nav-height;
      padding: 0px 15px;

      .router-link,
      .cta-copy {
        @include cc-title();

        height: $nav-height;
        line-height: $nav-height;
        margin: 0px 15px;

        &.router-link-exact-active {
          color: $color-brand !important;
        }

        &.disabled {
          display: none;
        }
      }

      .cta-copy {
        background-color: $color-brand !important;
        color: $color-white;
        padding: 0px 30px;
        margin-left: auto;

        &:focus {
          outline: none;
        }

        &:hover {
          background-color: $color-brand-hover !important
        }

        &:active {
          opacity: 0.7;
        }
      }
    }
  }
</style>



