<template>
  <textarea class="hs-tinymce" :id="id"></textarea>
</template>

<script>
  import Vue from 'vue';

  import tinymce from 'tinymce/tinymce';
  import '@/assets/i18n/tinymce/nl';
  import 'tinymce/themes/modern/theme';

  function returnContent() {
    const self = this;

    if (!this.wrapValue) {
      return self.changedContent;
    }
    return {
      value: self.changedContent,
      isDirty: tinymce.get(this.id).isDirty(),
      isChanged: self.initialContent !== self.changedContent,
    };
  }

  export default {
    name: 'tinymce',
    props: {
      id: {
        type: String,
        default() {
          return 'tinymce-' + this._uid;
        },
      },
      options: Object,
      content: {
        type: String,
      },
      wrapValue: Boolean,
      defaultOnEmpty: Boolean,
    },
    data() {
      return {
        initialContent: null,
        changedContent: null,
      };
    },
    methods: {
      returnContent,
    },
    watch: {
      /*
      content() {
        tinymce.get(this.id).setContent(this.content);
      },
      */
    },
    mounted() {
      const self = this;

      const DEFAULT_CONFIG_SETUP = (editor) => {
        editor.on('NodeChange Change KeyUp', (e) => {
          self.changedContent = tinymce.get(this.id).getContent();

          if (self.defaultOnEmpty === true && self.changedContent === '') {
            tinymce.get(this.id).setContent(self.initialContent);
            return;
          }
          self.$emit('input', self.returnContent());
          self.$emit('change', self.returnContent());
        });
        editor.on('init', (e) => {
          tinymce.get(this.id).setContent(self.content || '');
          self.initialContent = tinymce.get(this.id).getContent();
        });
      };

      // Initial configuration
      const options = typeof this.options === 'object' ? Object.assign({}, this.options) : {};

      // Default set skin false
      options.skin = false;

      // Set defaults
      options.selector = '#' + this.id;

      // Merge setup
      options.setup = typeof options.setup === 'function' ? ($editor) => {
        DEFAULT_CONFIG_SETUP($editor);
        options.setup($editor);
      } : DEFAULT_CONFIG_SETUP;

      // Init on next tick
      Vue.nextTick(() => tinymce.init(options));
    },
    beforeDestroy() {
      tinymce.execCommand('mceRemoveEditor', false, this.id);
    },
  };
</script>
<style lang="scss">

  .hs-tinymce {

    // TinyMCE
    @import '../../../node_modules/tinymce/skins/lightgray/skin.min.css';
    @import '../../../node_modules/tinymce/skins/lightgray/content.inline.min.css';
  }

</style>



