<template>
  <label class="app-label-link">
    <a :href="link" @click.stop target="_blank">
      <slot></slot>
      <i>
        <font-awesome-icon :icon="icons.infoCircle"/>
      </i>
    </a>
  </label>
</template>

<script>

  import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
  import { faInfoCircle } from '@fortawesome/fontawesome-free-solid';

  export default {
    name: 'app-label-link',
    components: {
      FontAwesomeIcon,
    },
    props: {
      link: String,
    },
    data() {
      return {
        icons: {
          infoCircle: faInfoCircle,
        },
      };
    },
  };
</script>



