<template>
  <div :class="['hs-tablerow',{open:isOpen},{hasDetails:hasDetails}]" @click="toggle()">
    <div class="summary">
      <div class="content">
        <div class="icon" v-if="icon">
          <img :src="icon"/>
        </div>
        <div class="info">
          <slot name="info">
            <div class="title">{{title}}</div>
            <div class="subtitle">{{subtitle}}</div>
          </slot>
        </div>
      </div>
      <div class="actions">
        <slot name="actions"/>
        <div v-if="hasDetails">
          <div class="summary-toggle">
            <i>
              <font-awesome-icon :icon="icons.angleDown"/>
            </i>
          </div>
        </div>
      </div>
    </div>
    <b-collapse :id="id" v-model="isOpen" class="details" v-if="hasDetails">
      <slot name="details"></slot>
    </b-collapse>
  </div>
</template>

<script>

  import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
  import { faAngleDown } from '@fortawesome/fontawesome-free-solid';

  import bCollapse from 'bootstrap-vue/es/components/collapse/collapse';

  export default {
    name: 'hs-tablerow',
    components: {
      FontAwesomeIcon,
      bCollapse,
    },
    props: {
      icon: String,
      title: String,
      subtitle: String,
    },
    data() {
      return {
        hasDetails: false,
        // eslint-disable-next-line no-underscore-dangle
        id: 'tablerow-' + this._uid,
        icons: {
          angleDown: faAngleDown,
        },
        isOpen: false,
      };
    },
    methods: {
      toggle() {
        if (!this.hasDetails) {
          return;
        }
        this.isOpen = !this.isOpen;
      },
    },
    created() {
      this.hasDetails = (this.$slots.details && this.$slots.details.length > 0);
    },
  };
</script>

<style lang="scss" scoped>

  @import "../../assets/scss/general";
  @import "../../assets/scss/mixins";

  .hs-tablerow {
    width: 100%;
    border-bottom: 1px solid #E9E9E9;
    overflow: hidden;

    @include clearfix();

    &.hasDetails {
      .summary{
        cursor: pointer;
      }
    }

    .summary {
      height: $tablerow-height;
      min-height: $tablerow-height;
      padding: 0px 1em;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      align-items: stretch;
      overflow: hidden;

      > * {
        display: inline-block;
      }

      .content {
        position: relative;
        display: flex;
        flex: 1 0 0;

        .icon {
          width: 40px;
          text-align: center;
          padding: 11px 0px 12px;
          margin-right: 1em;

          img {
            height: 25px;
            width: 25px;
            max-width: 100%;
          }
        }

        .info {
          flex: 1 0 0;
          padding-top: 10px;

          .title {
            font-weight: 500;
            font-size: 13px;
          }

          .subtitle {
            margin-top: 1px;
            font-size: 11px;
            padding: 0px;
          }
        }
      }

      .actions {
        display: flex;
        flex: 0 0 0;
        flex-direction: row;
        flex-wrap: nowrap;

        .summary-toggle {
          height: $tablerow-height;
          line-height: $tablerow-height;
          margin-right: -16px;

          i {
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            width: $tablerow-height;

            @include anim-clickable(1.3,1.1);

            svg {
              font-size: 16px;
              width: 20px;
              transition: all 0.3s;
            }
          }
        }
      }
    }

    &.open {
      .actions {
        svg {
          transform: rotate(180deg);
        }
      }
    }
  }
</style>



