<template>
  <label class="hs-checkbox">
    <input type="checkbox" @change="$emit('change', currentValue)" v-model="currentValue" :disabled="disabled">
    <span class="input-box">
        <svg class="input-box-tick" viewBox="0 0 16 16">
            <path fill="none" d="M1.7,7.8l3.8,3.4l9-8.8"></path>
        </svg>
    </span>
    <slot></slot>
  </label>
</template>

<script>
  export default {
    name: 'hs-checkbox',
    props: {
      value: Boolean,
      disabled: Boolean,
    },
    computed: {
      currentValue: {
        get() {
          return this.value;
        },
        set(val) {
          this.$emit('input', val);
        },
      },
    },
  };
</script>

<style lang="scss">
  .hs-checkbox {
    position: relative;

    input[type=checkbox] {
      opacity: 0;
      position: absolute;

      &:checked + .input-box {

        .input-box-tick > path {
          opacity: 1;
          stroke-dashoffset: 0;
        }
      }

      &:focus + .input-box {
        box-shadow: 0 0 2px 3px rgba(115, 185, 255, 0.69);
      }
    }

    input[type=checkbox] + .input-box {
      display: inline-block;
      border: 1px solid #000000;
      border-radius: 14%;
      margin: 0;
      padding: 0;
      width: 1em;
      height: 1em;
      background: #fff;
      overflow: hidden;
      vertical-align: -5%;
      user-select: none;

      > .input-box-tick {
        width: 100%;
        height: 100%;

        > path {
          opacity: 0;
          stroke: #000;
          stroke-width: 2.3px;
          stroke-dashoffset: 20;
          stroke-dasharray: 20;
          transition: stroke-dashoffset 0.15s ease-in;
        }
      }
    }
    &:checked + label > .input-box > .input-box-tick > path {
      opacity: 1;
      stroke-dashoffset: 0;
    }

    &:focus + label > .input-box {
      box-shadow: 0 0 2px 3px rgba(115, 185, 255, 0.69);
    }

    &:disabled + label {
      opacity: 0.7;
    }

  }
</style>



