/* eslint-disable class-methods-use-this */

// eslint-disable-next-line import/extensions
import main from './main.js';
import { isFunction } from 'lodash';

let elementId;

function isValidElementId($elementId) {
  if (typeof $elementId !== 'string' || $elementId.indexOf('#') !== 0
    || global.document.getElementById($elementId.substring(1)) === null) {
    throw new Error('Invalid element id given. Element id not found.');
  }
}

function isValidListener($fn, $event) {
  if (!isFunction($fn)) {
    console.log('Unable to register change listener for: ' +
      ($event || 'null') + '. Changelistener must be a function.');
    return false;
  }
  return true;
}

export default class ConsentCookieConfigurator {

  constructor($el, $options) {
    isValidElementId($el);

    elementId = $el;
    this.setOptions($options);
    this.scriptUrl = main.scriptUrl;
  }

  setConfig($config) {
    main.setConfig($config);
    return this;
  }

  getConfig() {
    return main.getConfig();
  }

  resetConfig() {
    main.resetConfig();
    return this;
  }

  setOptions($options) {
    main.setOptions($options);
    return this;
  }

  getOptions() {
    return main.getOptions();
  }

  onChange($fn) {
    if (!isValidListener($fn, 'onChange')) {
      return this;
    }

    const self = this;
    main.onChange(($payload) => {
      $fn(self, $payload);
    });
    return this;
  }

  onMounted($fn) {
    if (!isValidListener($fn, 'onMounted')) {
      return this;
    }

    const self = this;
    main.onMounted(($payload) => {
      $fn(self, $payload);
    });
    return this;
  }

  mount() {
    main.mount(elementId);
    return this;
  }

  mountScript($elementId) {
    main.mountScript($elementId);
    return this;
  }

}



