import Vue from 'vue';
import { isFunction, isEmpty, initial } from 'lodash';

function arrayToObject($array, $keyFn) {
  const object = {};

  if (!$array || !$array.length) {
    return object;
  }
  for (let i = 0; i < $array.length; i += 1) {
    let key = null;
    if (typeof $keyFn === 'function') {
      key = $keyFn(i, $array[i], $array);
    }
    key = key || i;
    object[key] = $array[i];
  }
  return object;
}

function getObject($object, $namespace, $default, $parent) {
  const defaultVal = $default || null;

  if (!$object) {
    return defaultVal;
  }
  if (typeof $namespace !== 'string') {
    return $object;
  }

  const nameSpaceArray = $namespace.split('.');
  let object = $object;
  let parent = null;
  for (let i = 0; i < nameSpaceArray.length; i += 1) {
    parent = object;
    object = object[nameSpaceArray[i]];

    if (!object) {
      return ($parent === true) ? parent : defaultVal;
    }
  }
  return ($parent === true) ? parent : object;
}

function getObjectValue($object, $namespace, $default) {
  return getObject($object, $namespace, $default);
}

function getObjectParent($object, $namespace, $default) {
  return getObject($object, $namespace, $default, true);
}

function deleteObjectProperty($root, $namespace, $removeEmptyParent, $isRecursive) {
  const namespaceSeperator = '.';
  const objectParent = getObjectParent($root, $namespace);

  if (objectParent) {
    const keys = $namespace.split(namespaceSeperator);
    const lastKey = keys[keys.length - 1];

    if (lastKey && objectParent[lastKey]) {
      if ($isRecursive === true && !isEmpty(objectParent[lastKey])) {
        return;
      }
      Vue.delete(objectParent, lastKey);
    } else {
      return;
    }
  }
  if ($removeEmptyParent === true) {
    deleteObjectProperty($root, initial($namespace.split(namespaceSeperator))
      .join(namespaceSeperator), $removeEmptyParent, true);
  }
}

function setObjectProperty($object, $namespace, $value, $createParent) {
  if (!$object || typeof $namespace !== 'string' || typeof $value === 'undefined') {
    return;
  }
  const nameSpaceArray = $namespace.split('.');
  let parent = null;
  let object = $object;
  // The last index is the name of the property we want to set so excluding it by length - 1
  for (let i = 0; i < nameSpaceArray.length - 1; i += 1) {
    parent = object;
    object = object[nameSpaceArray[i]];

    if (!object) {
      if ($createParent !== true) {
        return;
      }
      Vue.set(parent, nameSpaceArray[i], {});
      object = parent[nameSpaceArray[i]];
    }
  }
  const key = nameSpaceArray[nameSpaceArray.length - 1];
  Vue.set(object, key, $value);
}

function createConfigComputedProperty($configKey, $getter, $setter, $removeOnDefault = true) {
  const configKey = $configKey;

  return {
    get() {
      const val = this.$services.config.getConfigVal(this.$store.state.config, configKey);
      if (isFunction($getter)) {
        return $getter.call(this, val, $configKey);
      }
      return val;
    },
    set($value) {
      const val = isFunction($setter) ? $setter.call(this, $value, $configKey) : $value;
      if (val === undefined) {
        return;
      }
      this.$services.config.updateConfig(configKey, val, $removeOnDefault);
    },
  };
}

function getNamespaces($key, $seperator = '.') {
  const namespaces = [];

  if (!$key || typeof $key !== 'string') {
    return namespaces;
  }
  const splitted = $key.split($seperator);
  splitted.forEach(($namespace, $index) => {
    // slice does not include the end, hence + 1
    namespaces.push(splitted.slice(0, $index + 1)
      .join($seperator));
  });
  return namespaces;
}

export default {
  arrayToObject,
  getObjectValue,
  getObjectParent,
  deleteObjectProperty,
  setObjectProperty,
  createConfigComputedProperty,
  getNamespaces,
};



