import utils from '@/internal/utils';
import * as constants from '@/internal/constants';

let vueInstance;
let vueStore;

function init($vueServices) {
  vueInstance = $vueServices.getVueInstance();
  vueStore = vueInstance.$store;
}

function getOptions() {
  return JSON.parse(JSON.stringify(vueInstance.$store.state.options));
}

function setOptions($options) {
  vueInstance.$store.commit('setOptions', $options);
}

function getOption($name, $default) {
  const val = utils.getObjectValue(vueInstance.$store.state.options, $name);
  return val || $default;
}

function isOption($name, $val) {
  return typeof $name !== 'undefined' && typeof $val !== 'undefined' && getOption($name) === $val;
}

export default {
  init,
  getOptions,
  setOptions,
  getOption,
  isOption,
};



