<template>
  <div class="configuration-panel apps">
    <div class="instructions">Selecteer de applicaties die data verzamelen op je website</div>
    <!-- Selected table -->
    <div class="container configuration">
      <div class="row selected-applications" v-show="selected.length > 0">
        <div class="subtitle">Geselecteerde applicaties:</div>
        <div class="list-selected">
          <TableRow class="application" v-for="item in selected" v-bind:key="item.name" :icon="item.icon"
                    :title="item.name" :subtitle="item.company">
            <div slot="actions" class="selected-actions">
              <div class="type form-group">
                <select class="form-control" v-model="item.consent.initstate">
                  <option value="optin">opt-in</option>
                  <option value="optout">opt-out</option>
                  <option value="alwayson">altijd aan</option>
                </select>
              </div>
              <div class="remove" @click="remove(item)">
                <i><font-awesome-icon :icon="icons.trash"/></i>
              </div>
            </div>
          </TableRow>
        </div>
        <small class="form-text text-muted">Selecteer per applicatie 'opt-in', 'opt-out' of 'altijd aan'. <a
          href="https://www.consentcookie.nl/documentation/configuratietool/applicatie/" target="_blank">Meer informatie</a></small>
      </div>
      <!-- Input filter -->
      <input class="form-control filter" type="text" v-model="filter" placeholder="Zoek een applicatie..."/>
      <!-- List -->
      <div class="list-applications">
        <TableRow class="new-application">
          <span slot="info">Mis je een applicatie?<a target="_blank"
            href="https://www.consentcookie.nl/aanmelden-van-consent-applicatie/">Klik hier</a></span>
        </TableRow>
        <TableRow class="application" v-for="item in filteredList" v-bind:key="item.name" :icon="item.icon"
                  :title="item.name" :subtitle="item.company">
          <div slot="actions" class="application-actions">
            <div class="add-application" v-if="item.status === 'accepted'">
              <button type="button" class="btn btn-add" @click="add(item)">Voeg toe</button>
            </div>
            <div class="check-status" v-if="item.status !== 'accepted'">
              <span>Zie status</span>
            </div>
          </div>
          <app-detail-top :application="item" slot="details" class="application-details"></app-detail-top>
        </TableRow>
      </div>
    </div>
  </div>
</template>

<script>

  import utils from '@/internal/utils';
  import * as constants from '@/internal/constants';
  import _ from 'lodash';

  import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

  import { faPlus } from '@fortawesome/fontawesome-free-solid';
  import { faTrashAlt } from '@fortawesome/fontawesome-free-regular';

  import TableRow from '@/components/general/hsTableRow';
  import AppDetailTop from '@/components/apps/AppDetailTop';

  function loadDefaultApps() {
    this.axios.get(this.$services.config.getAppsResourceUrl())
      .then(($response) => {
        if ($response.status !== 200) {
          return;
        }
        this.apps = $response.data;
        if (this.apps) {
          loadSelected.call(this);
        }
      });
  }

  function loadSelected() {
    const consents = this.$services.config.getConfigVal(this.$store.state.config, constants.DEFAULT_CONFIG_KEY_APPS_CONSENT);
    const appMap = utils.arrayToObject(this.apps, ($index, $value) => $value.id);
    _.each(consents, ($val, $key) => {
      if (appMap[$key]) {
        this.$set(appMap[$key], 'consent', consents[$key]);
        this.selected.push(appMap[$key]);
      }
    });
  }

  function filteredList() {
    const self = this;
    let searchFiltered = self.apps || [];

    // Filter by search input
    if (self.filter !== '') {
      searchFiltered = searchFiltered.filter($app => $app.name.toLowerCase()
        .includes(self.filter.toLowerCase()));
    }
    // Default filter if already selected
    return searchFiltered.filter($app => self.selected.indexOf($app) < 0);
  }

  function add($app) {
    if (this.selected.indexOf($app) >= 0) {
      return;
    }
    const consent = this.$services.config.createConsent($app);
    this.$set($app, 'consent', consent);
    this.selected.push($app);
  }

  function remove($app) {
    this.$services.config.removeConsent($app);
    this.$delete($app, 'consent');
    this.selected.splice(this.selected.indexOf($app), 1);
  }

  function updateAnonymous($item) {
    // Currently set true. Set to false
    if ($item.consent.anonymous === true) {
      this.$delete($item.consent, 'anonymous');
    } else {
      // Currently set false: Set true
      this.$set($item.consent, 'anonymous', true);
    }
  }

  export default {
    name: 'apps',
    components: {
      FontAwesomeIcon,
      TableRow,
      AppDetailTop,
    },
    data() {
      return {
        filter: '',
        selected: [],
        apps: null,
        icons: {
          add: faPlus,
          trash: faTrashAlt,
        },
      };
    },
    computed: {
      filteredList,
      config() {
        return this.$store.state.config;
      },
    },
    methods: {
      add,
      remove,
      updateAnonymous,
    },
    mounted() {
      loadDefaultApps.call(this);
    },
  };
</script>

<style lang="scss" scoped>

  @import '../../src/assets/scss/general';
  @import '../../src/assets/scss/mixins';

  #consentcookie-configurator {

    .apps {

      .info {
        margin: 30px auto 20px;
        text-align: center;
      }

      .selected-applications {
        margin: 0px auto 1em;

        .subtitle {
          @include cc-title();
          margin: 5px 5px;
          padding: 0px;
        }

        .list-selected {
          width: 100%;

          .selected-actions {
            height: $tablerow-height;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-items: center;

            .type {

              width: 95px;
              margin: 0px 10px;
              font-size: 13px;

              select {
                margin: 3px 0px;
                background: $color-white;
                text-align: center;
              }
            }

            .remove {
              height: $tablerow-height;
              line-height: $tablerow-height;
              margin-right: -16px;

              i {
                display: inline-block;
                vertical-align: middle;
                text-align: center;
                width: $tablerow-height;

                @include anim-clickable(1.3, 1.1);

                svg {
                  font-size: 16px;
                  width: 20px;
                  transition: all 0.3s;
                }
              }
            }
          }
        }

        .text-muted {

          margin: 15px 0px 0px 72px;

          a{
            color: $color-brand;
          }
        }
      }
    }

    .filter {
      margin: 15px auto 0px;
      font-size: 0.8125em;
      padding: 0.5em 0.75em;
    }

    .list-applications {
      width: 670px;
      margin: 0px -15px;
      padding: 0px 15px;

      .new-application {

        /deep/ .info {
          padding: 0px 0px 0px 56px;
          line-height: 48px;
          color: #6c757d!important;
          font-size: 12.8px;

          a {
            margin-left: 5px;
            color: $color-brand;
          }
        }
      }

      .application {

        .application-details {
          padding: 5px 16px 10px;
          font-size: 13px;
        }

        .application-actions {
          width: auto;

          .add-application {
            transition: .3s all ease-in-out;
            height: 48px;
            width: 0px;
            line-height: 48px;
            position: relative;
            overflow: hidden;

            .btn-add {
              @include anim-clickable(1, 0.9);

              width: 80px;
              background-color: $color-brand !important;
              color: $color-white;
              padding: 5px 10px;
              font-size: 13px;
              margin-left: auto;
              text-align: center;
            }
          }

          .check-status {
            font-size: 13px;
            white-space: nowrap;
            padding: 8px 10px;
            margin: 8px 0px;
            color: $color-white;
            background: $color-inactive;
            width: 80px;
            text-align: center;
            opacity: 0.5;
          }
        }

        &:hover {

          /deep/ .actions {

            i {
              transition: .15s all ease-in-out;
              transform: scale(1.3);
            }
          }

          .application-actions {

            .add-application {
              width: 80px;
            }
          }

          .check-status {
            opacity: 1;
          }

        }
      }
    }
  }
</style>



