<template>
  <div class="app-detail-top">
    <div class="row default-info">
      <div class="col-8">
        <div class="description">
          <label>Beschrijving</label>
          <div v-html="application.description"/>
        </div>
        <div class="website" v-if="application.links">
          <label>Website</label>
          <a class="site-link" :href="application.links.site" @click.stop target="_blank">{{application.links.site}}</a>
        </div>
      </div>
      <div class="col-4">
        <div class="status">
          <app-label-link :link="links.status">Status</app-label-link>
          <div>
            <span>Applicatie: </span><span class="status-info">{{ status }}</span>
          </div>
          <div>
            <span>Plugin: </span><span class="status-info">{{ pluginStatus }}</span>
          </div>
        </div>
        <a class="link-more-info" href="javascript:void(0)" @click.stop="toggle()">Detail info</a>
      </div>
    </div>
    <b-collapse :id="id" v-model="isOpen">
      <div class="row more-info">
        <div class="col-6">
          <div class="category">
            <app-label-link :link="links.category">Categorie(ën)</app-label-link>
            <ul v-if="application.category && application.category.length > 0">
              <li v-for="item in application.category" v-bind:key="item">
                {{item.split(',')[1]}}
              </li>
            </ul>
            <div v-if="!application.category || application.category.length == 0"><i>Geen informatie beschikbaar</i></div>
          </div>
        </div>
        <div class="col-6">
          <div class="datahandling">
            <app-label-link :link="links.dataHandling">Gegevensverwerking</app-label-link>
            <ul v-if="application.dataHandling && application.dataHandling.length > 0">
              <li v-for="item in application.dataHandling" v-bind:key="item.type">
                {{ item.type }}
              </li>
            </ul>
            <div v-if="!application.dataHandling || application.dataHandling.length == 0"><i>Geen informatie beschikbaar</i></div>
          </div>
        </div>
      </div>
      <div class="row kb-cta" v-if="application.links && application.links.kb">
        <div class="col-sm-12"><i>Is er iets niet juist en/of wil je een bijdrage leveren aan deze informatie? <a
          @click.stop target="_blank"
          :href="application.links.kb">Klik hier</a></i></div>
      </div>
    </b-collapse>
  </div>
</template>

<script>

  import bCollapse from 'bootstrap-vue/es/components/collapse/collapse';

  import appLabelLink from '../../components/apps/AppLabelLink';

  const DEFAULT_LINK_INFO_STATUS = 'https://www.consentcookie.nl/documentation/configuratietool/applicatie/';
  const DEFAULT_LINK_INFO_CATEGORY = 'https://www.consentcookie.nl/documentation/configuratietool/applicatie/';
  const DEFAULT_LINK_INFO_DATAHANDLING = 'https://www.consentcookie.nl/documentation/configuratietool/applicatie/';

  const DEFAULT_APPLICATION_STATUSSES = {
    new: 'Nieuw',
    accepted: 'Geaccepteerd',
    rejected: 'Afgewezen',
    'under revision': 'In revisie',
    active: 'Actief',
  };

  export default {
    name: 'app-detail-top',
    components: {
      bCollapse,
      appLabelLink,
    },
    props: {
      application: Object,
    },
    data() {
      return {
        // eslint-disable-next-line no-underscore-dangle
        id: 'app-detail-top' + this._uid,
        isOpen: false,
        links: {
          status: DEFAULT_LINK_INFO_STATUS,
          category: DEFAULT_LINK_INFO_CATEGORY,
          dataHandling: DEFAULT_LINK_INFO_DATAHANDLING,
        },
      };
    },
    computed: {
      status() {
        return DEFAULT_APPLICATION_STATUSSES[this.application.status];
      },
      pluginStatus() {
        if (this.application.plugins && this.application.plugins.length > 0) {
          return 'Beschikbaar';
        }
        return 'Niet beschikbaar';
      },
    },
    methods: {
      toggle() {
        this.isOpen = !this.isOpen;
      },
    },
  };
</script>

<style lang="scss">

  @import "../../assets/scss/general";

  #consentcookie-configurator {

    .app-detail-top {

      ul {
        padding-left: 1.6em;
      }

      label {
        display: block;
        font-weight: 500;
        margin-top: 0.5em;
        margin-bottom: 0.1em;

        a {
          color: $color-font;
        }
      }

      .site-link {
        display: block;
      }

      .default-info {
        position: relative;

        .status {
          .status-info{
            font-style: italic;
          }
        }

        .link-more-info {
          position: absolute;
          left: 15px;
          bottom: 0;
        }
      }

      .more-info,
      .kb-cta {
        margin-top: 10px;
      }
    }
  }
</style>



