<template>
  <div class="configuration-panel contents">
    <div class="instructions">Kies per scherm de gewenste tekst die je wilt tonen in ConsentCookie</div>
    <div class="container configuration">
      <tabs class="vertical">
        <tab name="Over">
          <div class="form-group row">
            <label for="content-info-title">Titel</label>
            <input class="form-control" type="text" id="content-info-title" v-model="infoTitle"/>
            <small class="form-text text-muted">Geef de titel voor de over-pagina op. <a
              href="https://www.consentcookie.nl/documentation/configuratietool/tekst/" target="_blank">Meer informatie</a></small>
          </div>
          <div class="form-group row">
            <label>Tekst</label>
            <tinymce v-model="infoText" ref="infoText" :options="editor.options" :content="orgInfoText"
                     :wrap-value="true" :default-on-empty="true"/>
            <small class="form-text text-muted">Geef de tekst voor de over-pagina op</small>
          </div>
        </tab>
        <tab name="Consent">
          <div class="form-group row">
            <hs-checkbox v-model="consentEnabled">Actief</hs-checkbox>
            <small class="form-text text-muted">Selecteer het vakje om een consentwall the tonen. <a
              href="https://www.consentcookie.nl/documentation/functionaliteiten/consentwall/" target="_blank">Meer informatie</a></small>
          </div>
          <div v-if="consentEnabled">
            <div class="form-group row">
              <label for="consent-title">Titel</label>
              <input class="form-control" type="text" id="consent-title" v-model="consentTitle"
                     :wrap-value="true" :default-on-empty="true"/>
              <small class="form-text text-muted">Geef de titel voor de consentwall op</small>
            </div>
            <div class="form-group row">
              <label for="consent-text">Tekst</label>
              <tinymce v-model="consentText" :options="editor.options" :content="orgConsentText"
                       :wrap-value="true" :default-on-empty="true"/>
              <small class="form-text text-muted">Geef de tekst op die getoond wordt</small>
            </div>
            <div class="form-group row">
              <label for="consent-button-text">Knop tekst</label>
              <input class="form-control" type="text" id="consent-button-text" v-model="consentButtonText"/>
              <small class="form-text text-muted">Geef de tekst voor de akkoord-knop op</small>
            </div>
            <div class="form-group row">
              <label for="consent-info-link">Info link</label>
              <input class="form-control" type="text" id="consent-info-link" v-model="consentInfoLink"/>
              <small class="form-text text-muted">Geef een link op met informatie over de cookie policy op jouw website
              </small>
            </div>
          </div>
        </tab>
      </tabs>
    </div>
  </div>
</template>

<script>

  import { Tabs, Tab } from 'vue-tabs-component';
  import hsCheckbox from '@/components/general/hsCheckbox';
  import tinymce from '@/components/general/hsTinymce';
  import { clone } from 'lodash';

  import utils from '@/internal/utils';
  import * as constants from '@/internal/constants';

  const DEFAULT_WYSIWYG_OPTIONS = {
    height: 300,
    menubar: false,
    branding: false,
    content_style: '.mce-content-body {font-family: Roboto, Arial, sans-serif !important;}',
    style_formats_merge: true,
    remove_linebreaks: true,
    language: 'nl',
    style_formats: [
      {
        title: 'Font Sizes',
        items: [
          { title: '10pt', inline: 'span', styles: { fontSize: '10px' } },
          { title: '12pt', inline: 'span', styles: { fontSize: '12px' } },
          { title: '14pt', inline: 'span', styles: { fontSize: '14px' } },
          { title: '18pt', inline: 'span', styles: { fontSize: '18px' } },
          { title: '24pt', inline: 'span', styles: { fontSize: '24px' } },
          { title: '36pt', inline: 'span', styles: { fontSize: '36px' } },
        ],
      },
    ],
  };

  const DEFAULT_CONFIG_KEY_RESOURCES_NL_INFO_TITLE = 'resources.nl.about.title';
  const DEFAULT_CONFIG_KEY_RESOURCES_NL_INFO_TEXT = 'resources.nl.about.text';

  const DEFAULT_CONFIG_KEY_GENERAL_CONSENTWALL_ENABLED = 'general.consentwall.enabled';
  const DEFAULT_CONFIG_KEY_RESOURCES_NL_CONSENT_TITLE = 'resources.nl.consent.title';
  const DEFAULT_CONFIG_KEY_RESOURCES_NL_CONSENT_TEXT = 'resources.nl.consent.text';
  const DEFAULT_CONFIG_KEY_RESOURCES_NL_CONSENT_BUTTON_TEXT = 'resources.nl.consent.button';
  const DEFAULT_CONFIG_KEY_RESOURCES_NL_CONSENT_INFO_LINK = 'resources.nl.consent.infolink';

  function setConsentEnabled($val, $key) {
    if ($val === false) {
      this.$services.config.removeConfig($key, true);
      this.$services.config.removeConfig(constants.DEFAULT_CONFIG_KEY_RESOURCES_CONSENT, true);
      return undefined;
    }
    return $val;
  }

  function ignoreIfDefault($wrapper, $key) {
    if (!$wrapper.isChanged) {
      // Remove key
      this.$services.config.removeConfig($key, true);
      return undefined;
    }
    return $wrapper.value;
  }

  export default {
    name: 'home',
    components: {
      tinymce,
      Tabs,
      Tab,
      hsCheckbox,
    },
    data() {
      return {
        editor: {
          options: DEFAULT_WYSIWYG_OPTIONS,
        },
        orgInfoText: '',
        orgConsentText: '',
      };
    },
    computed: {
      infoTitle: utils.createConfigComputedProperty(DEFAULT_CONFIG_KEY_RESOURCES_NL_INFO_TITLE),
      infoText: utils.createConfigComputedProperty(DEFAULT_CONFIG_KEY_RESOURCES_NL_INFO_TEXT, null, ignoreIfDefault),
      consentEnabled: utils.createConfigComputedProperty(DEFAULT_CONFIG_KEY_GENERAL_CONSENTWALL_ENABLED, null, setConsentEnabled),
      consentTitle: utils.createConfigComputedProperty(DEFAULT_CONFIG_KEY_RESOURCES_NL_CONSENT_TITLE),
      consentText: utils.createConfigComputedProperty(DEFAULT_CONFIG_KEY_RESOURCES_NL_CONSENT_TEXT, null, ignoreIfDefault),
      consentButtonText: utils.createConfigComputedProperty(DEFAULT_CONFIG_KEY_RESOURCES_NL_CONSENT_BUTTON_TEXT),
      consentInfoLink: utils.createConfigComputedProperty(DEFAULT_CONFIG_KEY_RESOURCES_NL_CONSENT_INFO_LINK),
    },
    created() {
      this.orgInfoText = String(this.infoText);
      this.orgConsentText = String(this.consentText);
    },
  };
</script>

<style lang="scss">

  @import '../assets/scss/mixins';
  @import '../assets/scss/general';

  #consentcookie-configurator {

    .configuration-panel.contents {

      .tabs-component.vertical {

        // col-sm-4 values
        .tabs-component-tabs {
          position: relative;
          -webkit-box-flex: 0;
          -ms-flex: 0 0 33.33333%;
          flex: 0 0 33.33333%;
          max-width: 33.33333%;

          &:after {
            position: absolute;
            top: 0;
            bottom: 0;
            right: 33.33333%;
            display: block;
            background: 1px #e6e7e1;
            width: 1px;
            content: '';
          }

          .tabs-component-tab{
            margin: 0px;
          }
        }

        // col-sm-8 values
        .tabs-component-panels {
          padding-top: 15px;
          -webkit-box-flex: 0;
          -ms-flex: 0 0 66.66667%;
          flex: 0 0 66.66667%;
          max-width: 66.66667%;
        }
      }

      .hs-checkbox {
        width: 100%;
      }

      .mce-panel {
        padding: 1px;
      }
    }
  }
</style>



