import { cloneDeep, isObject, isFunction, forEachRight, initial } from 'lodash';

import utils from '@/internal/utils';
import * as constants from '@/internal/constants';

let vueInstance;
let vueStore;

function parseConfig($config) {
  try {
    return JSON.parse($config);
  } catch ($e) {
    return null;
  }
}

function init($vueServices) {
  vueInstance = $vueServices.getVueInstance();
  vueStore = vueInstance.$store;
}

function setConfig($config) {
  const config = typeof $config === 'object' ? $config : parseConfig($config);
  if (!isObject(config)) {
    throw new Error('Invalid config. Config object must be a valid JSON string or Javascript object.');
  }
  vueInstance.$store.commit('setConfig', $config);
}

function getConfig() {
  return JSON.parse(JSON.stringify(vueInstance.$store.state.config));
}

function resetConfig() {
  vueInstance.$store.commit('resetConfig');
}

function getConfigVal($config, $key) {
  const val = utils.getObjectValue($config, $key);
  return val || getDefault($key);
}

function getDefault($key) {
  return cloneDeep(utils.getObjectValue(constants.DEFAULT_CONFIG, $key));
}

function updateConfig($key, $val, $removeOnDefault) {
  // Chechk if the new value is the default
  if ($removeOnDefault === true && $val === getDefault($key)) {
    removeConfig($key, true);
    return;
  }
  vueStore.commit('updateConfig', {
    key: $key,
    value: $val,
  });
}

function removeConfig($key, $removeEmptyParents = false) {
  vueStore.commit('removeConfig', { key: $key, removeEmptyParents: $removeEmptyParents });
}

function createConsent($app) {
  const consent = {
    initstate: constants.DEFAULT_APPS_CONSENT_OPT_VALUE,
  };
  updateConfig(getConsentKey($app.id), consent);
  return consent;
}

function removeConsent($app) {
  removeConfig(getConsentKey($app.id));
}

function getAppsResourceUrl() {
  return utils.getObjectValue(vueStore.state.config, 'apps.endpoint',
    vueInstance.$services.options.getOption(constants.DEFAULT_OPTIONS_KEY_APPS_ENDPOINT,
      constants.DEFAULT_APPS_ENDPOINT));
}

function getConsentKey($id) {
  return constants.DEFAULT_CONFIG_KEY_APPS_CONSENT + '.' + $id;
}

export default {
  init,
  setConfig,
  getConfig,
  resetConfig,
  getConfigVal,
  getDefault,
  updateConfig,
  removeConfig,
  createConsent,
  removeConsent,
  getAppsResourceUrl,
};



