<template>
  <div class="configuration-panel script">
    <div class="instructions">Plak het onderstaande ConsentCookie script in de header van jouw website. Het script is al gekopieerd. <a
      href="https://www.consentcookie.nl/documentation/configuratietool/script/" target="_blank">Meer informatie</a></div>
    <div class="configuration container">
      <scriptView/>
    </div>
  </div>
</template>

<script>

  import scriptView from '@/components/general/hsScriptView';

  export default {
    components: {
      scriptView,
    },
  };
</script>

<style lang="scss" scoped>

  #consentcookie-configurator {

    .configuration-panel.script {

      .instructions {
        margin-bottom: 0px;
      }

      .configuration.container {
        width: 100%;
        max-width: 100%;
        padding: 0px 30px;

      }
    }
  }

</style>



