<template>
  <div class="configuration-panel design ">
    <div class="instructions">Kies het gewenste design voor ConsentCookie</div>
    <div class="container configuration">
      <div>
        <label class="section">Layout</label>
        <div class="form-group row">
          <label for="layout-position" class="col-sm-4 col-form-label">Positie</label>
          <div class="col-sm-8">
            <select id="layout-position" class="form-control" v-model="layoutPosition">
              <option value="right">Rechts</option>
              <option value="left">Links</option>
            </select>
            <small class="form-text text-muted">Kies de positie van ConsentCookie op je website</small>
          </div>
        </div>
      </div>
      <div>
        <label class="section">Kleurenschema</label>
        <div class="form-group row justify-content-center">
          <div class="col-sm-4 color-select">
            <label for="color-primary">Hoofdkleur</label>
            <color-picker v-model="primaryColor" id="color-primary"/>
          </div>
          <div class="col-sm-4 offset-1 color-select">
            <label for="color-primary">Secundaire kleur</label>
            <color-picker v-model="secondaryColor"/>
          </div>
          <div class="col-sm-12">
            <small class="form-text text-muted">Kies de kleuren voor ConsentCookie. <a href="https://www.consentcookie.nl/doc/consentcookie/#9751" target="_blank">Meer informatie</a></small>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  import { Chrome } from 'vue-color';
  import utils from '@/internal/utils';
  import * as constants from '@/internal/constants';

  const COLORNAMES_BY_CONFIG_KEY = {};
  COLORNAMES_BY_CONFIG_KEY[constants.DEFAULT_CONFIG_KEY_DESIGN_COLORSCHEME_PRIMARY] = 'prinary';
  COLORNAMES_BY_CONFIG_KEY[constants.DEFAULT_CONFIG_KEY_DESIGN_COLORSCHEME_SECONDARY] = 'secondary';


  function getColorString($color) {
    if ($color && $color.rgba && $color.rgba.r && $color.rgba.g && $color.rgba.b && $color.rgba.a && $color.rgba.a !== 1) {
      return getRgba($color.rgba);
    } else if ($color && $color.hex) {
      return $color.hex;
    }
    return '';
  }

  function getRgba($rgba) {
    return 'rgba(' + $rgba.r + ',' + $rgba.g + ',' + $rgba.b + ',' + $rgba.a + ')';
  }

  function getColor($val, $key) {
    return $val;
  }

  function setColor($val, $key) {
    this.$set(this.colors, COLORNAMES_BY_CONFIG_KEY[$key], $val);
    return getColorString($val);
  }

  export default {
    name: 'home',
    components: {
      'color-picker': Chrome,
    },
    data() {
      return {
        colors: {
          primary: null,
          secondary: null,
        },
      };
    },
    computed: {
      layoutPosition: utils.createConfigComputedProperty(constants.DEFAULT_CONFIG_KEY_DESIGN_LAYOUT_POSITION),
      primaryColor: utils.createConfigComputedProperty(constants.DEFAULT_CONFIG_KEY_DESIGN_COLORSCHEME_PRIMARY, getColor, setColor),
      secondaryColor: utils.createConfigComputedProperty(constants.DEFAULT_CONFIG_KEY_DESIGN_COLORSCHEME_SECONDARY, getColor, setColor),
    },
  };
</script>

<style lang="scss" scoped>

  #consentcookie-configurator {

    .design {

      label{

        &.section{
          text-transform: uppercase;
          font-weight: 600;
          font-size: 13px;
          letter-spacing: 0.9px;
        }
      }

      .color-select {
        padding: 10px;

        label {
          display: block;
          text-align: center;
          margin-bottom: 0.5rem;
        }

        > * {
          font-family: Roboto, Arial, sans-serif !important;
          box-shadow: none !important;
          border-radius: 0px !important;
        }

        .vc-chrome {
          width: auto;
        }

        .form-text {
          text-align: center;
        }
      }
    }
  }
</style>



