import Vue from 'vue';

import axios from 'axios';
import VueAxios from 'vue-axios';
import VueEvents from 'vue-events';
import VueHighlightJS from 'vue-highlightjs';
import VueClipboard from 'vue-clipboard2';

import { cloneDeep, isFunction } from 'lodash';
import * as constants from '@/internal/constants';

import App from '@/views/Main';
import ScriptView from '@/components/general/hsScriptView';

import router from './internal/router';
import store from './internal/store';
import services from './internal/services';

import css from './assets/scss/_ccc.scss';

Vue.use(VueAxios, axios);
Vue.use(VueHighlightJS);
Vue.use(VueEvents);
Vue.use(VueClipboard);

Vue.config.productionTip = false;

const MainComponent = Vue.extend(App);
const vue = new MainComponent({
  router,
  store,
  services,
});

function mount($elementId) {
  vue.$mount($elementId);
}

function mountScript($elementId) {
  const ScriptComponent = Vue.extend(ScriptView);
  new ScriptComponent({
    store,
  }).$mount($elementId);
}

function setOptions($options) {
  vue.$store.commit('setOptions', $options);
}

function getOptions() {
  return JSON.parse(JSON.stringify(vue.$store.state.options));
}

function getConfig() {
  return vue.$services.config.getConfig();
}

function setConfig($config) {
  return vue.$services.config.setConfig($config);
}

function resetConfig() {
  vue.$services.config.resetConfig();
}

function onChange($fn) {
  if (!isFunction($fn)) {
    console.error(new Error('Unable to register change listener. Changelistener must be a function.'));
    return;
  }
  vue.$events.$on('change', $fn);
}

function onMounted($fn) {
  if (!isFunction($fn)) {
    console.error(new Error('Unable to register change listener. Changelistener must be a function.'));
    return;
  }
  vue.$events.$on('mounted', $fn);
}

export default {
  vue,
  mount,
  mountScript,
  setOptions,
  getOptions,
  setConfig,
  getConfig,
  resetConfig,
  onChange,
  onMounted,
  scriptUrl: constants.DEFAULT_CLIENT_SCRIPT_HOST,
};



