import Vue from 'vue';
import Vuex from 'vuex';

import { cloneDeep, mergeWith } from 'lodash';

import utils from '@/internal/utils';
import * as constants from '@/internal/constants';

Vue.use(Vuex);

function notifyChange() {
  // eslint-disable-next-line no-underscore-dangle
  const vue = this._vm;
  const store = this;

  // Notify changed
  vue.$events.$emit('change', JSON.parse(JSON.stringify(store.state.config)));
}

function mergeConfig($state, $payload) {
  const config = $state.config;
  $state.config = mergeWith({}, config, $payload);
}

function mergeOptions($state, $payload) {
  const options = $state.options;
  mergeWith(options, $payload);
}

const store = new Vuex.Store({
  state: {
    config: {},
    options: {},
  },
  mutations: {
    setConfig($state, $payload) {
      mergeConfig($state, $payload);
    },
    resetConfig($state, $payload) {
      $state.config = JSON.parse(JSON.stringify(constants.DEFAULT_INITIAL_CONFIG));
    },
    updateConfig($state, $payload) {
      utils.setObjectProperty($state.config, $payload.key, $payload.value, true);
    },
    removeConfig($state, $payload) {
      utils.deleteObjectProperty($state.config, $payload.key, $payload.removeEmptyParents);
    },
    setOptions($state, $payload) {
      mergeOptions($state, $payload);
    },
  },
  getters: {
    script: $state => constants.DEFAULT_CLIENT_SCRIPT_INTEGRATION_PREFIX
      + JSON.stringify($state.config, null, 3) + constants.DEFAULT_CLIENT_SCRIPT_INTEGRATION_SUFFIX,
    config: $state => $state.config,
  },
});


// Enabled watcher
store.watch($state => $state.config, ($newVal, $oldVal) => {
  notifyChange.call(store);
}, {
  deep: true,
});

export default store;



